import React, { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
	IconArrowRightDashed,
	IconChevronRight,
	IconDeviceLaptop,
	IconMoon,
	IconSun,
} from '@tabler/icons-react';
import { useSearch } from '@/context/search-context';
import { useTheme } from '@/context/theme-context';
import {
	CommandDialog,
	CommandEmpty,
	CommandGroup,
	CommandInput,
	CommandItem,
	CommandList,
	CommandSeparator,
} from '@/components/ui/command';
import { sidebarData } from './layout/data/sidebar-data';
import { ScrollArea } from './ui/scroll-area';

export function CommandMenu() {
	const navigate = useNavigate();
	const { setTheme } = useTheme();
	const { open, setOpen } = useSearch();

	const runCommand = useCallback(
		(command) => {
			setOpen(false);
			command();
		},
		[setOpen]
	);

	return (
		<CommandDialog modal open={open} onOpenChange={setOpen}>
			<CommandInput placeholder="Type a command or search..." />
			<CommandList>
                                <ScrollArea type="hover" className="tw-h-72 tw-pr-1">
					<CommandEmpty>No results found.</CommandEmpty>
					{sidebarData.navGroups.map((group) => (
						<CommandGroup key={group.title} heading={group.title}>
							{group.items.map((navItem, i) => {
								if (navItem.url) {
									return (
										<CommandItem
											key={`${navItem.url}-${i}`}
											value={navItem.title}
											onSelect={() => {
												runCommand(() =>
													navigate({
														to: navItem.url,
													})
												);
											}}
										>
                                                                                        <div className="tw-mr-2 tw-flex tw-h-4 tw-w-4 tw-items-center tw-justify-center">
                                                                                                <IconArrowRightDashed className="tw-text-muted-foreground/80 tw-size-2" />
											</div>
											{navItem.title}
										</CommandItem>
									);
								}

								return navItem.items?.map((subItem, i) => (
									<CommandItem
										key={`${navItem.title}-${subItem.url}-${i}`}
										value={`${navItem.title}-${subItem.url}`}
										onSelect={() => {
											runCommand(() =>
												navigate({ to: subItem.url })
											);
										}}
									>
                                                                                <div className="tw-mr-2 tw-flex tw-h-4 tw-w-4 tw-items-center tw-justify-center">
                                                                                        <IconArrowRightDashed className="tw-text-muted-foreground/80 tw-size-2" />
										</div>
										{navItem.title} <IconChevronRight />{' '}
										{subItem.title}
									</CommandItem>
								));
							})}
						</CommandGroup>
					))}
					<CommandSeparator />
					<CommandGroup heading="Theme">
						<CommandItem
							onSelect={() => runCommand(() => setTheme('light'))}
						>
							<IconSun /> <span>Light</span>
						</CommandItem>
						<CommandItem
							onSelect={() => runCommand(() => setTheme('dark'))}
						>
                                                        <IconMoon className="tw-scale-90" /> <span>Dark</span>
						</CommandItem>
						<CommandItem
							onSelect={() =>
								runCommand(() => setTheme('system'))
							}
						>
							<IconDeviceLaptop /> <span>System</span>
						</CommandItem>
					</CommandGroup>
				</ScrollArea>
			</CommandList>
		</CommandDialog>
	);
}
